.site-sections {
  box-sizing: border-box;
  padding-left: 20px;
  margin-right: 25%;

  h3 {
    margin-bottom: 26px;
  }
}

.site-sections__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  align-items: start;
  flex-direction: column;
  flex-wrap: wrap;
  box-sizing: border-box;
  margin-bottom: 58px;
  max-height: 115px;
  width: 100%;
}

.site-sections__item {
  margin-right: 65px;
}

.site-sections__link {
  display: block;

  &:hover,
  &:focus {
    color: $accent;
  }

  &:active {
    color: rgba($accent, 0.6);
  }
}

.site-sections__toggle {
  display: none;
}

@media (max-width: $tablet) {
  .site-sections {
    width: 50%;
    padding-left: 25px;
    box-sizing: border-box;
    margin-right: 0;
  }

  .site-sections__list {
    max-height: 100%;
    margin-bottom: 57px;
  }
}

@media (max-width: $mobile) {
  .site-sections {
    width: 100%;
    padding: 0 15px;
    border-bottom: 1px solid $grey;
    position: relative;

    h3 {
      margin: 0;
      padding: 29px 0 27px;
    }
  }

  .site-sections__title {
    cursor: pointer;
  }

  .site-sections__list {
    display: none;
  }

  .site-sections__toggle {
    display: block;
    position: absolute;
    top: 29px;
    right: 15px;
    width: 16px;
    height: 16px;
    background-color: transparent;
    border: none;
    outline: none;
    background-image: url("../img/plus.svg");
    background-repeat: no-repeat;
    background-position: center;
  }

  .site-sections--opened {
    .site-sections__list {
      display: flex;
      padding: 4px 0 26px 0;
      margin-bottom: 0;
    }

    .site-sections__toggle {
      background-image: url("../img/minus.svg");
    }
  }

  .site-sections--nojs {
    .site-sections__list {
      display: flex;
      padding: 4px 0 26px 0;
      margin-bottom: 0;
    }

    .site-sections__toggle {
      display: none;
    }
  }
}
